* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Times New Roman', Times, serif;
  color: black;
}


body {
  min-height: 100vh;
  background: rgb(216, 216, 216);
  overflow-x: scroll;
  
}

.navigation {
  position: absolute;
  top: 0px;
  left: 0px;
  bottom: 0px;
  width: 70px;
  height: 950px; 
  background: #F6F6F6;
  border-radius: 10px;
  box-sizing: initial;
  border-left: 5px solid red;
  transition: 0.3s;
  overflow-x: hidden;


}

.navigation:hover {
  width: 300px;

}


.navigation ul {
  position: absolute;
  top: 0px;
  left: 0;
  padding-left: 5px;
  padding-top: 40px;
  width: 100%;

}

.navigation ul li {
  position: relative;
  list-style: none;
  width: 100%;
  color: rgb(224, 223, 223);
 
}

.navigation ul li:hover {
  background: #fff;
  border-radius: 20px 0px 0px 20px;
  font-size: large;
  /* color:rgb(224, 223, 223) ; */
}

.navigation ul li a {
  position: relative;
  display: block;
  width: 100%;
  color: rgb(224, 223, 223);
  display: flex;
  text-decoration: none;

}

.navigation ul li a:hover {
  color: black;
  font-size: larger;
}

.navigation ul li.active a {

  color: black;
}


.icon {
  position: relative;
  display: block;
  min-width: 60px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  font-size: 20px;
}

.title {
  position: relative;
  display: block;
  padding-left: 10px;
  height: 60px;
  line-height: 60px;
  white-space: normal;

}


#sear{
  height: 50px;
  margin-top: 50px;
  width: 350px;
  padding-left: 60px;
  word-spacing: 3px;
  font-size: larger;
  border-radius: 10px;
  position: static;
 
}

#filter{
  border: 1px solid black;
  height: 40px;
  width: 90px;
  border-radius: 20px;
  display: flex;
  padding: 10px;
  margin-left: 60px;
  background: #fff;
}

#symbol{
 
  position: relative;
  margin-right: -40px;
  margin-bottom: -40px;
  

}


#box{
  border: 1px solid black;
  height: 220px;
  width: 700px;
  margin-left: 200px;
  border-radius: 50px;
  background: #F4F5F9  ;
  border: none;
}

#box2{
  border: 1px solid black;
  height: 200px;
  width: 300px;
  margin-left: 200px;
  margin-top: 30px;
  border-radius: 30px;
 
  
}

#box5{
  margin-left:-120px ;
